buyAdd(id, index) {
this.buyFalge = id;
var lists = this.lists;
var buyNumber = Number(lists[index].buyNumber);
buyNumber = buyNumber + 1;
lists[index].buyNumber = buyNumber;
},

buy_minute(id, index) {
this.buyFalge = id;
var lists = this.lists;
var buyNumber = lists[index].buyNumber;
if (buyNumber > 0) {
buyNumber = buyNumber - 1;
lists[index].buyNumber = buyNumber;
}
