

import CheckIcon from 'vux/src/components/check-icon/index.vue'
import XAddress from 'vux/src/components/x-address/index.vue'
import ChinaAddressV4Data from 'vux/src/datas/china_address_v4.json'
import value2name from 'vux/src/filters/value2name.js'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
directives: {
TransferDom
},
components: {
CheckIcon,XAddress,
},
name: "addressManagement",
data() {
return {
showAddress:false,
addressValue:[],
addressData: ChinaAddressV4Data,
address:'',
demo1: false, //默认
AddressMFalge: false 
};
},
methods: {

logHide (str) {
if(str){
var name = value2name(this.addressValue, ChinaAddressV4Data);
this.address = name;
}
},
saveAddress(){},

openAddressM() {
this.AddressMFalge = true;
},
goToAddressManagement(){
this.showAddress = true;
},

closeAddressMFalge(e) {

if(!e.target.offsetParent){
this.AddressMFalge = false;
this.showAddress = false;
}

}
},
created() {
console.log(this);
settitle("地址管理");


},

mounted() {


}
};



